}
.drawer__content-item::after {
  content: "";
  display: inline-block;
  width: 0.875rem;
  height: 0.875rem;
  position: absolute;
  background-image: url(/src/images/common/icon-right.svg);
  background-size: contain;
  background-repeat: no-repeat;
  vertical-align: middle;
  right: 0;
}

