.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 前へ次への矢印カスタマイズ */
.swiper-button-prev,
.swiper-button-next {
  height: 50px;
  width: 50px;
}

.swiper-button-next {
  right: -1.375rem;
}

.swiper-button-prev {
  left: -1.375rem;
}

/* 前へ次への矢印カスタマイズ */
.swiper-button-prev:after,
.swiper-button-next:after {
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  height: 50px;
  margin: auto;
  width: 50px;
}

/* 前への矢印カスタマイズ */
.swiper-button-prev:after {
  background-image: url(/src/images/top/arrow-left.svg);
}

/* 次への矢印カスタマイズ */
.swiper-button-next:after {
  background-image: url(/src/images/top/arrow-right.svg);
}

/* 画像サイズ調整 */
.swiper-slide img {
  height: auto;
  width: 100%;
}

.swiper-pagination,
.swiper-pagination-bullets,
.swiper-pagination-horizontal {
  display: flex;
  flex-direction: column;
  left: -21px;
}

